/* Базовые стили для Telegram WebApp */
:root {
  --tg-theme-bg-color: var(--tg-theme-secondary-bg-color, #ffffff);
  --tg-theme-text-color: var(--tg-theme-text-color, #000000);
  --tg-theme-hint-color: var(--tg-theme-hint-color, #999999);
  --tg-theme-link-color: var(--tg-theme-link-color, #2481cc);
  --tg-theme-button-color: var(--tg-theme-button-color, #2481cc);
  --tg-theme-button-text-color: var(--tg-theme-button-text-color, #ffffff);
}

/* Адаптация под Telegram WebApp */
.telegram-app {
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  min-height: 100vh;
  padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
}

/* Стили для карточек */
.telegram-app .card {
  background-color: var(--tg-theme-bg-color);
  border: 1px solid var(--tg-theme-hint-color);
  border-radius: 10px;
}

/* Стили для кнопок */
.telegram-app .button {
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
  cursor: pointer;
  transition: opacity 0.2s;
}

.telegram-app .button:hover {
  opacity: 0.9;
}

/* Стили для текста */
.telegram-app .text-primary {
  color: var(--tg-theme-text-color);
}

.telegram-app .text-secondary {
  color: var(--tg-theme-hint-color);
}

.telegram-app .link {
  color: var(--tg-theme-link-color);
  text-decoration: none;
}

/* Адаптация под разные платформы */
.android .telegram-app {
  font-family: Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", sans-serif;
}

.ios .telegram-app {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
}

/* Безопасные отступы для разных устройств */
@supports (padding: max(0px)) {
  .telegram-app {
    padding-left: max(env(safe-area-inset-left), 16px);
    padding-right: max(env(safe-area-inset-right), 16px);
    padding-top: max(env(safe-area-inset-top), 16px);
    padding-bottom: max(env(safe-area-inset-bottom), 16px);
  }
} 